<template>
  <div class="memory">
    <h2>{{ memoryArticle.title }}</h2>
    <div v-html="memoryArticle.content"></div>
  </div>
</template>

<script>
import axios from "axios";
export default {
  name: "Memory",
  data(){
    return {
      memoryArticle:{}
    }
  },
  created() {
    axios.get('/MemoryArticles/'+this.$route.params.id)
      .then((data) =>{
        // console.log(data.data)
        this.memoryArticle = data.data
      })
  }
}
</script>

<style scoped>
.memory h2 {
  padding: 15px 0;
}
.memory >>> h3 {
  padding: 10px 0;
}
.memory >>> p {
  text-indent: 2em;
}
</style>



